.sidebar{
    flex: 1;
    border-right: 0.5px solid rgb(231,228,228);
    min-height: 100vh;
    background-color: white;

    .top{
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;

        .logo{
            font-size: 20px;
            font-weight: bold;
            color: #730273;

        }
    }

    hr{
        height: 0;
        border: 0.5px solid rgb(239, 239, 239);
    }

    .center{
        padding-left: 10px;
        
        ul{
            list-style: none;

            .title{
                font-size: 10px;
                font-weight: bold;
                color: #999;
                margin-top: 15px;
                margin-bottom: 5px;
            }

            li{
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;
                transition:all .4s;

                &:hover{
                    background-color: #ece8ff;
                }

                .icon{
                    font-size: 18px;
                    color: #730273;
                }

                span{
                    font-size: 13px;
                    font-weight: 600;
                    color: #888;
                    margin-left: 10px;
                }


            }
        }
    }

    .bottom{
        display: flex;
        align-items: center;
        margin: 10px;

        .colorOption{
            width: 20px;
            height: 20px;
            border-radius: 5px;
            border: 1px solid #730273;
            margin: 5px;
            cursor: pointer;


            &:nth-child(1){
                background-color: whitesmoke;
            }
            &:nth-child(2){
                background-color: #333;
            }
            &:nth-child(3){
                background-color: #730273;
            }
        }
    }
}